$(function () {
    $('.accordion__title').click(function(e) {
        e.preventDefault();

        var $this = $(this);

        if ($this.parent().hasClass('is-show')) {
            $this.parent().removeClass('is-show');
            $this.parent().find('.accordion__hidden').slideUp(350);
        } else {
            $this.parent().addClass('is-show');
            $this.parent().find('.accordion__hidden').slideDown(350);
        }
    });

    $('#header-toggle-call').on('click', function() {
        $('body').toggleClass('is-fixed');
        $(window).scrollTop(0);
        $(this).toggleClass('is-active');
        $('.header__toggle').toggleClass('is-open');
    });

    $('.header__toggle').on('click', 'li.is-dropdown', 'li.has-child', function (event) {
        $(event.target).closest('li').toggleClass('is-open').siblings('li').removeClass('is-open');
    });

    $('.sidebar__menu h4').on('click', function() {
        $(this).closest('.sidebar__menu').toggleClass('is-open');
        $(this).next('nav').slideToggle();
    });
});